.new-note {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-top: 1px solid black;
}

.new-note button {
  -moz-appearance: none;
  -webkit-appearance: none;
  border: none;
  width: 100%;
  font-size: 1em;
  background-color: inherit;
  cursor: pointer;
  padding: 0.5em 0;
  color: #0089ff;
  outline: none;
}

.new-note button:hover {
  background-color: whitesmoke;
}


